body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    /* Background */
    background-color: #282c34;

    /* Display content in the middle */
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    width: 100vw; /* 100% viewport width */
    height: 100vh; /* 100% viewport height */
}
.iGo {
    /* iGo Cascading Styles */
    /* Background */
    background-color: white;

    /* Display */
    width: 45mm;
    height: 45mm;
    overflow: hidden;

    /* Text */
    text-align: center;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.App-logo {
	width: 125px;
	height: 150px;
	margin-top: 10px;
}

.Button-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.Button-container button {
    padding-top: 50%;
    padding-bottom: 50%;
}

.iGo-InitialScreen h1{
    margin-bottom: 0;
    margin-top: 0;
}

.iGo-InitialScreen a{
    color: black;
    text-decoration: none;
}
.MainMenu ul{
   width: 45mm;
   height: 8mm;
}

.MainMenu button {
    pointer-events: none;
}

.MainMenu-Friends {
    height: 45mm;
}

.MainMenu-Events {
    height: 45mm;
}

.MainMenu-Notifications {
    height: 45mm;
}

.MainMenu-Entry {
    height: 35mm;
}

.MainMenu-Entry input {
    height:100%;
}
.Friends {
    height: 45mm;
    overflow-y: scroll;
}

.Friends a{
    color: black;
    text-decoration: none;
}

.Friends-QR {
    height: 45mm;
}

.Friends-QR img {
    max-height: 40mm;
    max-width: 40mm;
}

.Friends-Title {
    border-bottom: black solid 1px;
}

.Friends-Entry {
    height: 11mm;
    text-align: left;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    border-bottom: black solid 1px;
    overflow: hidden;
}

.Friends-Entry a {
    width: 30mm;
}

.Friends-Entry img {
    width: 15mm;
}

.Profile {
    height: 45mm    ;
    overflow-y: scroll;
}

.Profile-List div {
    border-bottom: black solid 1px;
}

.Friends-Sort-Find {
    height: 40mm;
    overflow-y: scroll;
}

.Header {
    height: 5mm;
}
.Events {

}
.Notifications {
    height: 40mm;
    overflow-y: scroll;
}

.Notifications div {
    overflow: hidden;
    height: 12mm;
}

.Notifications a {
    color: black;
    text-decoration: none;
}

.Notifications-Entry {
    border: 1px solid black;
    text-align: left;
}

.Notifications-Delete {
    border: 1px solid black;
    background-color: red;
	position:absolute;
	margin-left: 12px;
	margin-top: 5px;
}

.Notifications-Open {
    background-color: aqua;
    line-height: 100%;
}

.Notifications-Title {
    border-bottom: 3px solid black;
    height: 5mm;
}
